/* ========================================================================
 * DOM-based Routing
 * Based on http://goo.gl/EUTi53 by Paul Irish
 *
 * Only fires on body classes that match. If a body class contains a dash,
 * replace the dash with an underscore when adding it to the object below.
 *
 * .noConflict()
 * The routing is enclosed within an anonymous function so that you can
 * always reference jQuery with $, even when in .noConflict() mode.
 * ======================================================================== */

(function($) {

  // Use this variable to set up the common and page specific functions. If you
  // rename this variable, you will also need to rename the namespace below.
  var Sage = {
    // All pages
    'common': {
      init: function() {
        // JavaScript to be fired on all pages

        $(document).ready(function(){
          $(".mainform").sticky({topSpacing:0});
        });

    },
      finalize: function() {
        // JavaScript to be fired on all pages, after page specific JS is fired

                $("#billing_first_name").val($("dd.variation-Firstname").text());
                $("#billing_last_name").val($("dd.variation-Lastname").text());
                $("#billing_phone").val($("dd.variation-Phone").text());
                $("#billing_email").val($("dd.variation-email").text());
                $("#order_comments").val($("dd.variation-Notes").text());



        $(document).on('facetwp-loaded', function() {

          $(function() {
             FWP.auto_refresh = false;
             FWP.loading_handler = function(params) {};
           });

          if (FWP.loaded) { // after the initial pageload
              FWP.parse_facets(); // load the values
              FWP.set_hash(); // set the new URL
              location.reload();
              return false;
          }

          $('.booking_date_day').val($(".facetwp-date-min").val().trim().split('-')[2]);
          $('.booking_date_month').val($(".facetwp-date-min").val().trim().split('-')[1]);
          $('.booking_date_year').val($(".facetwp-date-min").val().trim().split('-')[0]);
          $('.booking_date_day').focus();

          $('.booking_to_date_day').val($(".facetwp-date-max").val().trim().split('-')[2]);
          $('.booking_to_date_month').val($(".facetwp-date-max").val().trim().split('-')[1]);
          $('.booking_to_date_year').val($(".facetwp-date-max").val().trim().split('-')[0]);
          $('.booking_to_date_day').focus();

          $(".facetwp-date-min").change(function() {                  // When the δατε is changed
              $('.booking_date_day').val($(this).val().trim().split('-')[2]);
              $('.booking_date_month').val($(this).val().trim().split('-')[1]);
              $('.booking_date_year').val($(this).val().trim().split('-')[0]);
              $('.booking_date_day').focus();
          });
          $(".facetwp-date-max").change(function() {                  // When the δατε is changed
              $('.booking_to_date_day').val($(this).val().trim().split('-')[2]);
              $('.booking_to_date_month').val($(this).val().trim().split('-')[1]);
              $('.booking_to_date_year').val($(this).val().trim().split('-')[0]);
              $('.booking_to_date_day').focus();
                    });



        //Isotope
        var $container = $('.grid'),
        $items = $('.grid_item');

        $container.isotope({
        itemSelector: '.grid_item',
        masonry: {
        columnWidth: '.grid-sizer',
        gutter: '.gutter-sizer'
        }
        });


        $items.click(function(){
        var $this = $(this);
        // don't proceed if already selected
        var $previousSelected = $('.selected');
        if ( !$this.hasClass('selected') ) {
        $this.removeClass('grid_item');
        $this.addClass('col-lg-12 selected menu-box');
        $('html,body').animate({
            scrollTop: $(".selected").offset().top},
            'slow');
        $('.booking_date_day').focus();

//window.scrollTo(0, $(".selected").offset().top);
        $previousSelected.removeClass('col-lg-12 selected menu-box');
        $previousSelected.addClass('grid_item');

        }


        // update sortData for new items size
        $container
        .isotope( 'updateSortData', $this )
        .isotope( 'updateSortData', $previousSelected )
        .isotope( 'layout', $this )
        .isotope();

        });



                //isotope animation
                var spotsNodeList = document.querySelectorAll(".grid_item");
                var spotsArray = [].slice.call(spotsNodeList); //BOOM, returns an array.


                //main timeline
                var tl = new TimelineMax();
                tl
                  .set(spotsArray, {x:70, autoAlpha:0})
                  //call function that returns a timeline
                  .add(spotsIn());

                // sets the animation for the array items and controls the time delay in between the items animating in
                function spotsIn(){
                  var child = new TimelineLite();
                      child.staggerTo(spotsArray, 0.5, {delay:2.75, ease: Elastic.easeOut.config(1, 1), x:0, autoAlpha:1}, 0.25);
                  //return the timeline
                  return child;

                }

        // layout Isotope after each image loads
        $container.imagesLoaded().progress( function() {
        $container.isotope('layout');
        });
        // Scroll to the top of the page after the page is refreshed


      //  var $datepicker = $('.booking_date_month');
      //  $datepicker.datepicker();
      //  $datepicker.datepicker('setDate', new Date(1985));



        });



        $('input.in_time').timepicker({
          timeFormat: 'h:mm p',
      interval: 30,
      minTime: '06',
      maxTime: '11:00pm',
      defaultTime: '12',
      startTime: '06:00',
      dynamic: false,
      dropdown: true,
      scrollbar: true
        });

        $('input.out_time').timepicker({
          timeFormat: 'h:mm p',
      interval: 30,
      minTime: '12',
      maxTime: '11:00pm',
      defaultTime: '12',
      startTime: '12:00',
      dynamic: false,
      dropdown: true,
      scrollbar: true
        });

        $(".wc-bookings-booking-form-button").click(function(){
          //  alert("The paragraph was clicked.");
            if (!$.trim($("#pick-up-airport").val())) {
                // textarea is empty or contains only white-space
                $("#pick-up-airport").val("-");
            }
            if (!$.trim($("#your-port").val())) {
                // textarea is empty or contains only white-space
                $("#your-port").val("-");
            }
            if (!$.trim($("#in-custom").val())) {
                // textarea is empty or contains only white-space
                $("#in-custom").val("-");
            }
            if (!$.trim($("#your-airport-out").val())) {
                // textarea is empty or contains only white-space
                $("#your-airport-out").val("-");
            }
            if (!$.trim($("#your-port-out").val())) {
                // textarea is empty or contains only white-space
                $("#your-port-out").val("-");
            }
            if (!$.trim($("#out-custom").val())) {
                // textarea is empty or contains only white-space
                $("#out-custom").val("-");
            }



        });

    }
    },
    // Home page
    'home': {
      init: function() {
        // JavaScript to be fired on the home page


      },
      finalize: function() {
        // JavaScript to be fired on the home page, after the init JS


      }
    },
    // About us page, note the change from about-us to about_us.
    'about_us': {
      init: function() {
        // JavaScript to be fired on the about us page
      }
    }
  };

  // The routing fires all common scripts, followed by the page specific scripts.
  // Add additional events for more control over timing e.g. a finalize event
  var UTIL = {
    fire: function(func, funcname, args) {
      var fire;
      var namespace = Sage;
      funcname = (funcname === undefined) ? 'init' : funcname;
      fire = func !== '';
      fire = fire && namespace[func];
      fire = fire && typeof namespace[func][funcname] === 'function';

      if (fire) {
        namespace[func][funcname](args);
      }
    },
    loadEvents: function() {
      // Fire common init JS
      UTIL.fire('common');

      // Fire page-specific init JS, and then finalize JS
      $.each(document.body.className.replace(/-/g, '_').split(/\s+/), function(i, classnm) {
        UTIL.fire(classnm);
        UTIL.fire(classnm, 'finalize');
      });

      // Fire common finalize JS
      UTIL.fire('common', 'finalize');
    }
  };

  // Load Events
  $(document).ready(UTIL.loadEvents);

})(jQuery); // Fully reference jQuery after this point.
